import React from 'react'
import { contact, section5Title, social } from '../../profile'
import cv from 'assets/cv.pdf'
import transcript from 'assets/transcript.pdf'
import resume from 'assets/resume.pdf'

const Contact = () => {
      
    return (
        <div className="parallax">
                <div data-aos="zoom-in-up" data-aos-once="true" className="git-form">
                    <>
                <div className="git-head-div text-center mx-auto">
                        <h1 id="Contact" className="git-head">{section5Title}</h1>
                </div>
                </>
                <div className="container">
            
                <div className="col-12 col-sm-6 half">
                    <div className="col text-center lead">
                        <div className='row-auto'>
                        <a href={resume} download="SiwakornBoonpamee_Resume.pdf"><button className="download">My Resume</button></a>
                        </div>
                        <div className='row-auto'>
                        <a href={cv} download="SiwakornBoonpamee_CV.pdf"><button className="download">My CV</button></a>
                        </div>
                        <div className='row-auto'>
                        <a href={transcript} download="SiwakornBoonpamee_transcript.pdf"><button className="download">My Transcript</button></a>
                        </div>
                    </div>
                </div>
                <div className="col-12 col-sm-6 half">
                    <p className="lead">
                        {contact.pitch}        
                    </p>
                <div className="d-flex justify-content-center align-items-center flex-column">
                <div className="inline-block" style={{margin:'5px 15px'}}>
                    {social.linkedin && <a title="Visit Linkedin profile" rel="noopener noreferrer" target="_blank"  href={social.linkedin}><i className="fab fa-linkedin fa-3x"></i></a>}
                    {social.facebook && <a title="Visit Facebok profile" rel="noopener noreferrer" target="_blank" href={social.facebook}><i className="fab fa-facebook fa-3x"></i></a>}
                    {social.instagram && <a title="Visit Instagram profile" rel="noopener noreferrer" target="_blank" href={social.instagram}><i className="fab fa-instagram fa-3x"></i></a>}
                    {social.github && <a title="Visit Github profile" rel="noopener noreferrer" target="_blank" href={social.github}><i className="fab fa-github fa-3x"></i></a>}<br/>
                </div>
                    {social.resume && <a title="Download Resume" href={social.resume} download><i className="fas fa-download"></i></a>}
                </div>
                </div>
            </div>
            </div>
            <p id="not-dark" className="Copy"><strong>{contact.copyright}</strong></p>
        </div>
    )
    
}
//<div className="git-cont row"></div>
/* <form action={contact.contactUrl ? contact.contactUrl : "https://formspree.io"} method={contact.contactUrl ? "POST" : "GET"}>
                        <input type="text" id="fname" name="firstname" placeholder="Your name" required></input>
                        <input type="mail" id="mailid" name="Email" placeholder="Email Address" required></input>
                        <input type="text" id="sub" name="Subject" placeholder="Subject" required></input>
                        <textarea id="msg" name="message" placeholder="Message" required></textarea>
                        <button style={{cursor: 'pointer'}} type="submit">Send Message</button>
                    </form> */
export default Contact
