import React from 'react'
import Particles from 'react-particles-js'
import { background } from '../../profile'

const ParticlesBackground = () => {
    return (
        <div className="particle">
            { background.type === 'Snow' && <Particles
            height="100vh"
            width="100%"
                params={{
                "particles": {
                    "number": {
                        "value": 75,
                        "density": {
                            "enable": false
                        }
                    },
                    "color": {
                        "value": "#000000"
                    },
                    "size": {
                        "value": 10,
                        "random": true
                    },
                    "move": {
                        "direction": "bottom",
                        "out_mode": "out"
                    },
                    "line_linked": {
                        "enable": false
                    }
                },
                "interactivity": {
                    "events": {
                        "onclick": {
                            "enable": true,
                            "mode": "remove"
                        }
                    },
                    "modes": {
                        "remove": {
                            "particles_nb": 5
                        }
                    }
                }
            }} />}
            { background.type === 'Particle' && <Particles
            height= "100vh"
            width= "100vw"
            params={{
            "particles": {
                "collisions": {
                    "enable": true
                },
                "number": {
                    "value": 100,
                    "density": {
                        "enable": false
                    }
                },
                "color": "#000",
                "size": {
                    "value": 5,
                    "random": true,
                    "anim": {
                        "speed": 4,
                        "size_min": 0.3
                    }
                },
                "line_linked": {
                    "enable": false,
                    "color": "#000"
                },
                "move": {
                    "random": true,
                    "speed": 1,
                    "direction": "bottom",
                    "out_mode": "out"
                }
            },
            "interactivity": {
                "events": {
                    "onhover": {
                        "enable": true,
                        "mode": "bubble"
                    },
                    "onclick": {
                        "enable": true,
                        "mode": "push"
                    }
                },
                "modes": {
                    "bubble": {
                        "distance": 250,
                        "duration": 2,
                        "size": 6,
                        "opacity": 0.4
                    },
                    "push": {
                        "particles_nb": 5
                    }
                },
                "retina_detect": true
            }
        }} />}
            { background.type === 'masked' && <Particles
            height= "100vh"
            width= "100vw"
            params={{
                "background": {
                  "color": {
                    "value": "#ffffff"
                  },
                  "image": "url('https://particles.js.org/images/background3.jpg')",
                  "position": "50% 50%",
                  "repeat": "no-repeat",
                  "size": "cover"
                },
                "backgroundMask": {
                  "cover": {
                    "color": {
                      "value": {
                        "r": 255,
                        "g": 255,
                        "b": 255
                      }
                    }
                  },
                  "enable": true
                },
                "fullScreen": {
                  "enable": false,
                  "zIndex": -10
                },
                "interactivity": {
                  "events": {
                    "onClick": {
                      "enable": true,
                      "mode": "push"
                    },
                    "onHover": {
                      "enable": true,
                      "mode": "bubble",
                      "parallax": {
                        "force": 60
                      }
                    }
                  },
                  "modes": {
                    "bubble": {
                      "distance": 400,
                      "duration": 2,
                      "opacity": 1,
                      "size": 100
                    },
                    "connect": {
                      "distance": 0
                    },
                    "grab": {
                      "distance": 400
                    }
                  }
                },
                "particles": {
                  "color": {
                    "value": "#ffffff"
                  },
                  "links": {
                    "color": {
                      "value": "#ffffff"
                    },
                    "distance": 150
                  },
                  "move": {
                    "attract": {
                      "rotate": {
                        "x": 600,
                        "y": 1200
                      }
                    },
                    "enable": true,
                    "path": {},
                    "outModes": {
                      "bottom": "out",
                      "left": "out",
                      "right": "out",
                      "top": "out"
                    }
                  },
                  "number": {
                    "density": {
                      "enable": true
                    },
                    "value": 80
                  },
                  "opacity": {
                    "animation": {
                      "speed": 1,
                      "minimumValue": 0.1
                    }
                  },
                  "size": {
                    "random": {
                      "enable": true
                    },
                    "value": {
                      "min": 1,
                      "max": 30
                    },
                    "animation": {
                      "speed": 40,
                      "minimumValue": 0.1
                    }
                  }
                }
              }} />}
        </div>
    )
}

export default ParticlesBackground
